import { Fragment, useContext, useEffect } from "react";
import React from "react";
import { Segment, Container, Header, Divider, Card } from "semantic-ui-react";
import { RootStoreContext } from "../../app/stores/RootStore";
import ProductCard from "./productCard";

const Dashboard = () => {
  const rootStore = useContext(RootStoreContext);
  const { loadCategories, categories, loadProducts, products } = rootStore.productStore;

  useEffect(() => {
    loadCategories();
    loadProducts();
  }, [loadCategories, loadProducts]);

  return (
    <Container style={{ marginTop: "150px" }}>
      {categories?.map((category) => (
         <Fragment key={category.id}>
        <Divider horizontal>
          <Header as="h2" >{category?.name}</Header>
        </Divider>
        <Card.Group itemsPerRow={4}>
        {products.filter(x => x.category.id == category.id).map(product => (
          <ProductCard key={product.id} product = {product}></ProductCard>
        ))}
        </Card.Group>
        
       </Fragment>
      ))}
    </Container>
  );
};

export default Dashboard;
