import React from "react";
import { Menu, Image, Container, Dropdown, Button, Icon } from "semantic-ui-react";
import { NavLink, Link } from "react-router-dom";
import logo from '../../assets/logo.png'

export const NavBar = () => {
  return (

    <Menu fixed="top" size='huge'>
      <Container>
      <Menu.Item>
          <Image src={logo} size="small" as={NavLink} to='/'/>
        </Menu.Item>
<Menu.Item position='right'>
<Dropdown text='Produkty' pointing className='link item'>
      <Dropdown.Menu>
        <Dropdown.Header>Kategorie</Dropdown.Header>
        <Dropdown.Item as={NavLink}
            to="/kategorie">Loft</Dropdown.Item>
        <Dropdown.Item as={NavLink}
            to="/kategorie">cośtam</Dropdown.Item>
        <Dropdown.Item as={NavLink}
            to="/kategorie">cośtam</Dropdown.Item>
      </Dropdown.Menu>
    </Dropdown>
</Menu.Item>
<Menu.Item>
<Button basic className="ttt">GALERIA</Button>
</Menu.Item>
<Menu.Item>
<Button color='facebook'>
      <Icon name='facebook' /> Facebook
    </Button>
</Menu.Item>

      </Container>
 
    </Menu>

  );
};
